<template>
  <div class="outBox">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow @click-left="$router.go(-1)" />

    <div class="order-box">
      <div class="address" @click="openAddress">
          <p>{{userAddress.address}}</p>
          <!-- <p>{{userAddress.receiver}}{{userAddress.phone}}</p> -->
          
          </div>

      <div class="order-content">
        <div class="order-item">
          <div class="items" v-for="(item,index) in orderData" :key="index">
            <div class="clearfix">
              <div class="fl pro-img">
                <img
                  class="auto-img"
                  :src="item.small_img"
                />
              </div>
              <div class="fl pro-info">
                <div class="fl">
                  <div class="names">
                    <div class="zh-name">{{item.name}}</div>
                    <div class="en-name">{{item.enname}}</div>
                  </div>
                  <div class="rule">{{item.rule}}</div>
                </div>
                <div class="fr">
                  <div class="price">￥{{item.price}}</div>
                  <div class="count">x{{item.count}}</div>
                </div>
              </div>
            </div>
          </div>
         

          <div class="order-total clearfix">
            <div class="fr clearfix">
              <span class="fl total-count">共计 {{counts}} 件商品 合计：</span>
              <span class="fl total-price">￥{{total}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-submit-bar :price="total*100" button-text="立即结算" @submit="nowClearing"/>

    <van-popup class="popup-box" v-model="isOpen" position="bottom">
      <van-address-list v-model="aid" :list="addressList" default-tag-text="默认" @click-item="selectAddress" @add="addNewAddress" class="addressList" />
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/clearing.less";
export default {
  data() {
    return {
      isOpen: false,
      sids: [],
      orderData:[],//结算商品
      total:null,//总金额
      counts:null,//商品总件数
      aid:'1',
      addressList:[],
      userAddress: {
        address: '请选择收货地址。',
        phone: '',
        receiver: ''
      },
    };
  },
  created() {
    let sids = this.$route.query.sids.split("-");
    
    this.sids = sids;
    //获取提交订单数据
    this.getPayOrderData(sids);
    //获取收货地址数据
    this.getAddressData()
  },
  methods: {
    //打开地址选择框
    openAddress() {
      this.isOpen = true;
    },

    //获取提交订单数据
    getPayOrderData(sids) {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 50000) {
              result.data.result.forEach(v => {
                  
                  this.counts += v.count;
                  this.total += v.count * v.price;
              })

              this.orderData = result.data.result;
          }
        })
        .catch(() => {
          this.$toast.clear();
          
        });
    },

    //获取收货地址数据
    getAddressData() {

      let tokenString = localStorage.getItem('__Tk');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'GET',
        url: '/findAddress',
        params: {
          appkey: this.appkey,
          tokenString
        }
      }).then(result => {
        this.$toast.clear();
        
        if (result.data.code == 20000) {
          result.data.result.forEach(v => {
            let currentAddress = {
              id: v.aid,
              tel: v.tel,
              name: v.name,
              address: v.province + v.city + v.county + v.addressDetail,
              isDefault: Boolean(v.isDefault)
            };

            //默认选择默认地址
            if (v.isDefault) {
              this.aid = v.aid;

              //如果存在默认地址，则默认选择默认地址
              this.userAddress.address = currentAddress.address;
              this.userAddress.phone = currentAddress.tel
              this.userAddress.receiver = currentAddress.name

            }
            
            this.addressList.push(currentAddress);
          })
        }
        
      }).catch(() => {
        this.$toast.clear();
        
      })
    },

    //选择收货地址
    selectAddress(item){
        
        // this.userAddress
        this.isOpen = false

        this.address = item.address;

        this.userAddress = {
            address: item.address,
            phone: item.tel,
            receiver: item.name
        }
    },
    
    //新增新收货地址
    addNewAddress(){
      this.$router.push({name:'address'})
    },

    //立即结算
    nowClearing(){

        // this.$router.push({name:'order'})
        if (this.userAddress.address == '请选择收货地址') {
            this.$toast('请选择收货地址');
            return;
        }
        
        
        let tokenString = localStorage.getItem('__Tk');

      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0
      })

      this.axios({
        method: 'POST',
        url: '/pay',
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
          phone: this.userAddress.phone,
          address: this.userAddress.address,
          receiver: this.userAddress.receiver
        }
      }).then(result => {
        this.$toast.clear();
        
        if (result.data.code === 60000) {
          this.$router.push({name: 'order'});
        }
      }).catch(() => {
        this.$toast.clear();
        
      })
    }
  }
};
</script>

<style lang="less" scoped>
</style>